@font-face {
  font-family:'Museo Sans Cyrillic';
  src: url('../fonts/Museo Sans Cyrillic 900.eot');
  src: url('../fonts/Museo Sans Cyrillic 900.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Museo Sans Cyrillic 900.woff2') format('woff2'),
  url('../fonts/Museo Sans Cyrillic 900.woff') format('woff'),
  url('../fonts/Museo Sans Cyrillic 900.ttf') format('truetype'),
  url('../fonts/Museo Sans Cyrillic 900.svg#Museo Sans Cyrillic 900') format('svg');
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-0525;
}

@font-face {
  font-family: 'icomoon';
  src:
    url('../fonts/icomoon.ttf?xupnpd') format('truetype'),
    url('../fonts/icomoon.woff?xupnpd') format('woff'),
    url('../fonts/icomoon.svg?xupnpd#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

